
"use client";
import React, { useState, useEffect, useRef } from "react";
import { motion, useMotionValue, useSpring, animate } from "motion";
import { cn } from "@/lib/utils";

export const Lens = ({
  children,
  className,
  hovering,
  setHovering,
}: {
  children: React.ReactNode;
  className?: string;
  hovering: boolean;
  setHovering: (hovering: boolean) => void;
}) => {
  const ref = useRef<HTMLDivElement>(null);

  const [isHovered, setIsHovered] = useState(false);

  const x = useMotionValue(0);
  const y = useMotionValue(0);

  const springConfig = {
    stiffness: 150,
    damping: 20,
    mass: 1,
  };

  const springX = useSpring(x, springConfig);
  const springY = useSpring(y, springConfig);

  useEffect(() => {
    if (!ref.current) return;
    const { width, height } = ref.current.getBoundingClientRect();
    const handleMouseMove = (e: MouseEvent) => {
      if (!ref.current) return;

      const { left, top } = ref.current.getBoundingClientRect();
      const relativeX = e.clientX - left;
      const relativeY = e.clientY - top;
      x.set(relativeX - width / 2);
      y.set(relativeY - height / 2);
    };

    const handleMouseEnter = () => {
      setIsHovered(true);
      setHovering(true);
    };

    const handleMouseLeave = () => {
      setIsHovered(false);
      setHovering(false);
      animate(x, 0, { duration: 0.3 });
      animate(y, 0, { duration: 0.3 });
    };

    ref.current.addEventListener("mousemove", handleMouseMove);
    ref.current.addEventListener("mouseenter", handleMouseEnter);
    ref.current.addEventListener("mouseleave", handleMouseLeave);

    return () => {
      if (ref.current) {
        ref.current.removeEventListener("mousemove", handleMouseMove);
        ref.current.removeEventListener("mouseenter", handleMouseEnter);
        ref.current.removeEventListener("mouseleave", handleMouseLeave);
      }
    };
  }, [x, y, setHovering]);

  return (
    <motion.div
      ref={ref}
      style={{
        x: springX,
        y: springY,
        scale: isHovered ? 1.1 : 1,
        boxShadow: isHovered
          ? "0px 25px 40px -12px rgba(0,0,0,0.3), 0px 15px 25px -15px rgba(0,0,0,0.2)"
          : "0px 10px 20px -10px rgba(0,0,0,0.1)",
      }}
      className={cn("relative w-full h-full rounded-2xl", className)}
    >
      {children}
    </motion.div>
  );
};
